.signInForm {
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin: 20px auto;
    width: 80%;
    height: 300px;
    background-color: #ede9d6;
    border-radius: 6px 6px;
    border: 4px solid #9e0e06;
  }

  .signInInput {
    width: 80%;
    margin: 0 auto 20px;
    padding: 2px 4px;
    border-radius: 3px 3px;
    color: #9e0e06;
  }

  .signInInput:focus {
    background-color: #9e0e06;
    color: white;

  }

  .signInSubmit {
    margin: 0 auto;
    background-color: #9e0e06;
    color:  white;
    width: 30%;
  }

  p:hover {
    color: #9e0e06;;
  }

  @media screen and (min-width: 500px) {
      .signInForm {
          width: 30%;
          height: 400px;
      }
  }